@import '../../styles/typography.scss';
@import '../../styles/colors.scss';

.header_wrapper {
  height: 8rem;
  min-height: 8rem;
  margin-bottom: 1rem;

  display: flex;
  align-items: center;
}

.time {
  position: absolute;
  right: 1rem;
  text-align: right;
  font-size: 2rem;
  color: $accentColor2;
  font-weight: bold;
}

@media only screen and (max-width: 1440px) {
  .header_wrapper {
    height: 5rem;
    min-height: 5rem;
  }
}
