@import '../../styles/typography.scss';
@import '../../styles/colors.scss';

.eventListElement {
  padding: 1rem;
  border-bottom: 1px dashed #e1e1e1;
  user-select: none;

  display: flex;

  background-color: $primaryBackgroundColor;
  width: 100%;

  &:hover {
    background-color: $accentColor1Dark;
  }

  &__icon {
    color: white;
    margin-top: 0.8rem;
  }

  &__time {
    color: $accentColor2;
    width: 6rem;
    font-size: 3rem;
    margin: 0.5rem 3rem 0.5rem 1rem;
  }

  &__text {
    color: $primaryColor;
    width: 100%;
    font-size: 3rem;
    margin: 0.5rem 3rem 0.5rem 1rem;
  }

  &__timeout {
    color: $accentColor1;
    margin-right: 1rem;

    &__text {
      position: absolute;
      right: 6rem;
      bottom: 3rem;
    }
  }
}
